.container {
  margin: 100px;
  align-items: center;
  text-align: center;
}

.field {
  display: flex;
  position: relative;
  margin: 5em auto;
  width: 45%;
  flex-direction: row;
  box-shadow: 1px 1px 0 rgb(122, 129, 128), 2px 2px 0 rgb(122, 129, 128),
    3px 3px 0 rgb(122, 129, 128), 4px 4px 0 rgb(122, 129, 128),
    5px 5px 0 rgb(122, 129, 128), 6px 6px 0 rgb(122, 129, 128),
    7px 7px 0 rgb(122, 129, 128);
}

.field > input[type="text"],
.field > .button {
  display: block;
  font: 1.2em "Montserrat Alternates";
}

.field > input[type="text"] {
  flex: 1;
  padding: 0.6em;
  border: 0.2em solid rgb(186, 196, 194);
}

.field > .button {
  padding: 0.6em 0.8em;
  background-color: rgb(186, 196, 194);
  color: white;
  border: none;
  cursor: pointer;
}

.field2 {
  margin-top: -50px;
}

.field2 > .button {
  padding: 0.6em 0.8em;
  background-color: rgb(186, 196, 194);
  color: white;
  border: none;
}

.field2 .button:hover{

  background-color: rgb(141, 146, 145);
}

.field .button:hover{

  background-color: rgb(141, 146, 145);
}

.buttonreset{
  background-color: rgb(186, 196, 194);
  border-style: none;
  width: 50px;
  height: 25px;
  margin-top: 5px;
  border-radius: 2px;
}

.buttonreset:hover{
  background-color: rgb(141, 146, 145);
  transform: translateY(2px);
}


