img {
  height: 150px;
  margin: auto;
  display: inline-block;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  font-weight: 400;
}

.container {
  display: flex;
  justify-content: space-around;
  margin-top: -50px;
  flex-direction: row;
  flex-wrap: wrap;
  flex: 2;
}

.botones {
  display: flex;
  justify-content: center;
  padding-bottom: 40px;
}

.pages {
  margin-top: -40px;
  margin-bottom: 30px;
  padding: 8px 16px;
  background-color: rgba(175, 172, 172, 0.65);
}

.pages:hover{
  background-color:rgb(141, 146, 145) ;
  transform: translateY(-2px);
}

figure {
  display: inline-block;
  width: 300px;
  margin-top: 40px;
  padding: 1em;
  border-style: groove;
  border-radius: 15px;
  background: rgb(141, 146, 145);
  text-align: left;
}
figure:visited{
  color: rgb(141, 146, 145);
}
figure:link{
  color: rgb(141, 146, 145);
}
.cardCaption {
  background-color: rgba(255, 255, 255, 0.65);
  padding: 1em;
  position: relative;
  border-radius: 0 0 3px 3px;
}

.cardImageContainer {
  background-color: rgba(255, 255, 255, 0.7);
  text-align: center;
  padding: 1em 1em 0;
  border-radius: 3px 3px 0 0;
}

.cardType {
  position: relative;
  top: -50px;
  transform: translateY(-50%);
  color: #f2f2f2;
  text-transform: capitalize;
  letter-spacing: 0.1em;
  padding: 0.25em;
  line-height: 1;
  border-radius: 2px;
  background: #bbbbbb;
}

.cardName {
  text-align: center;
  font-size: 1.5em;
  font-weight: 700;
  letter-spacing: 0.02em;
  text-transform: capitalize;
}

.types {
  display: flex;
  justify-content: space-around;
}

figure:hover {
  background: rgb(172, 175, 174);
  box-shadow: 0px 13px 30px -15px #000;
  transform: rotate3d(1, 1, 1, 25deg);;
}
